<template lang="pug">
#mainContent
    // 
    .main--sidebar
        // props 
        Sidebar

    // (  +  )
    .main--container
        Breadcrumb
        router-view( name="ContainerView" )
</template>

<script>
import  { mapGetters }  from    'vuex'

import Sidebar                  from '@/components/Home/MainContent/Sidebar'
import Breadcrumb               from '@/components/Home/MainContent/Breadcrumb'
const components = { Sidebar, Breadcrumb }

export default {
    mounted: function() {
        this.getSideBarList()
    },
    methods: {
        // : , 
        getSideBarList() {
            this.$store.dispatch({
                type    : 'getSideBarList'
                // ,attrObj : argumentsArr      // session; 
            })
        }
    },
    components: components
}
</script>

<style lang="sass" scoped>
@import '../../sass/main'

#mainContent
    position: fixed
    +w-h( 100%, inherit )
    +mT( 60px )
    .main--sidebar
        +floatL
        +w-h( $D-sidebarWidth, 100% )
        +bC( $C-theme )
    .main--container
        padding: $D-mainContainerPadding
        +mL( $D-sidebarWidth )
        min-width: 760px
        height: inherit
        +bC( $C-base )
</style>



