import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/Login'
import Home from '@/views/Home'
//
import UpdPwd from '@/pages/Other/UpdPwd'  //-
//
import AddUser from '@/pages/User/AddUser'                    //  - 
import UpdUser from '@/pages/User/UpdUser'                    //  - 
import SelUser from '@/pages/User/SelUser'                    //  - 
//
import RewardList from '@/pages/Reward/RewardList'               //  - 
import AddReward from '@/pages/Reward/AddReward'                //  - 
import UpdReward from '@/pages/Reward/UpdReward'                //  - 
//
import WorkOrderList from '@/pages/WorkOrder/WorkOrderList'         //  - 
import Pending from '@/pages/WorkOrder/Pending'               //  - 
//
import DescList from '@/pages/Score/DescList'                  //  - 
import SelByClass from '@/pages/Score/SelByClass'                //  - 
import SelByNum from '@/pages/Score/SelByNum'                  //  - 

Vue.use(Router)

export default new Router({
    routes: [
        {
            path: '/',
            component: Login
        },
        {
            path: '/Home',
            name: '',
            component: Home,
            children: [
                {
                    path: '/',
                    components: {
                        ContainerView: AddUser
                    }
                },
                // 
                {
                    path: '/AddUser',
                    components: {
                        ContainerView: AddUser
                    }
                }, {
                    path: '/UpdUser',
                    components: {
                        ContainerView: UpdUser
                    }
                }, {
                    path: '/SelUser',
                    components: {
                        ContainerView: SelUser
                    }
                },
                // 
                {
                    path: '/RewardList',
                    components: {
                        ContainerView: RewardList
                    }
                }, {
                    path: '/AddReward',
                    components: {
                        ContainerView: AddReward
                    }
                }, {
                    path: '/UpdReward',
                    components: {
                        ContainerView: UpdReward
                    }
                },
                // 
                {
                    path: '/WorkOrderList',
                    components: {
                        ContainerView: WorkOrderList
                    }
                }, {
                    path: '/Pending',
                    components: {
                        ContainerView: Pending
                    }
                },
                // 
                {
                    path: '/DescList',
                    components: {
                        ContainerView: DescList
                    }
                }, {
                    path: '/SelByClass',
                    components: {
                        ContainerView: SelByClass
                    }
                }, {
                    path: '/SelByNum',
                    components: {
                        ContainerView: SelByNum
                    }
                },
				{
                    path: '/UpdPwd',
                    components: {
                        ContainerView: UpdPwd
                    }

                }
            ]
        }
    ]
})



